h1, h4, .ui.container p, .ui.header, 
.ui.sub.header {
    font-family: 'Roboto', sans-serif;
}

.portfolio-body {
    padding-bottom: 225px;
}
.welcome-home {
    padding: 45px 0px;
}
.portfolio-title .portfolio-header {
    padding: 5em .8em 3em .8em;
    letter-spacing: 2px;
    font-weight: 800;
}

.portfolio-header h4.sub.header {
    color: rgba(0,0,0, 0.5);
    letter-spacing: 2px;
    margin-top: 5px;
}

.jump-top {
    float: right;
    padding: 10px;
    margin-bottom: 15px;
}
.ui.theme-orange.button {
    background-color:rgb(238, 142, 78);
    color: rgb(255, 255, 255);
    font-size: 14px;
    letter-spacing: 2px;
}

.ui.secondary.menu .item {
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 2px;
    line-height: 15px;
    font-size: 14px;
}

.portfolio-title {
    background-image: linear-gradient( 111deg,  
    rgba(255,255,255,1) 78%, rgba(44,204,176,0.5) 75% );
}

.project-select {
    padding: 3em 0em;
}

.project-body, .project-details {
    padding-top: 50px;
}

@media only screen and (max-width: 769px) {  
    .portfolio-title .portfolio-header{
        padding: 1.5em .3em 0.5em  .3em;
    }
}

@media only screen and (max-width: 525px) {  
    .portfolio-title .portfolio-header{
        padding: 1.5em .3em 0.5em  .3em;
    }
}